.about {
    background-color: #EBD9FF;
    opacity: 80%;
    width: 80vw;
    padding: 0 100px;
    font-size: 16px;
    overflow-y: hidden;
    position: relative;
    left: 10%;
}

.about p,
.about h2,
.about h3 {
    background-color: inherit;
}

.about h2 {
    text-align: center;
}